import React from "react";
import styled from "styled-components";
//Styled components premade
import { About, Description, Hide, Image } from "../components/styles";
//Framer motion
import { motion } from "framer-motion";
//Animation
import {
  titleAnimation,
  fade,
  profilePicAnimation,
} from "../animations/animation";
//Profile image
import profileImage from "../assets/images/perfil2.jpg";
//Components
import Wave from "./Wave";
import MoreAbout from "./MoreAbout";
//Hooks
import { useScroll } from "../hooks/useScroll";

import CvPdf from "../assets/images/german-saracca-cv-ingles.pdf";

const AboutSection = () => {
  const [element, control] = useScroll();
  return (
    <div>
      <About>
        <Description>
          <motion.div>
            <Hide>
              <motion.h2 variants={titleAnimation}>
                Hi! I am Germán Saracca
              </motion.h2>
            </Hide>
            <Hide>
              <motion.h2 variants={titleAnimation}>
                I am a <span>Fullstack</span> web
              </motion.h2>
            </Hide>
            <Hide>
              <motion.h2 variants={titleAnimation}>developer</motion.h2>
            </Hide>
          </motion.div>
          <motion.p variants={fade}>
            I invite you to know a little bit more about me by seeing my
            portfolio.
          </motion.p>
          <motion.p variants={fade}>
            You are able to{" "}
            <a href={CvPdf} download="CV_german-saracca">
              download my CV
            </a>{" "}
            for contact info.
          </motion.p>
        </Description>
        <Image>
          <motion.img
            variants={profilePicAnimation}
            src={profileImage}
            alt="profile"
          />
        </Image>
        <Wave />
      </About>
      <MoreAboutMe ref={element} variants={fade} animate={control}>
        <h2>
          <span>More</span> about me
        </h2>
        <MoreAbout
          key={1}
          title="English"
          description={[
            {
              desc: "Advanced use of English, Pre-First.",
              small: "“Welcome to English” private school. 7 years.",
            },
          ]}
        />
        <MoreAbout
          key={2}
          title="Education"
          description={[
            {
              desc: "Fullstack Web Developer, Acamica.",
              small:
                " Collab with Globant, Wolox, Mercadolibre and GlobalLogic. -32653201- title number. March 2020 - December 2020.",
            },
            { desc: "React JS, Coderhouse.", small: "2 months." },
          ]}
        />
        <MoreAbout
          key={3}
          title="Job Experience"
          description={[
            {
              desc:
                "Owner and founder of 'Libero', customized sports clothing, Tandil. APRIL, 2014 - JULY ,2020 . I started this business doing silk-screen-printing on t-shirts at home. Then the business grew up and I started doing customized sports clothing. Along the path I learn how to sell, take a customer idea and bring it to the screen and then give him the final product fulfilling all their expectations.Due to the current pandemic creating downturn of sports activities, I decided tod start a Full Stack Web Developer career in Acamica and I feel passionate about it, that is why I am looking forward to getting my first IT job and keeping on learning to become a MERN developer.Adaptable personality to easily become part of a work group and hard worker.I think it´s all about wanting to learn and putting it in practice. ",
            },
          ]}
        />
      </MoreAboutMe>
    </div>
  );
};

const MoreAboutMe = styled(motion.div)`
  /* min-height: 100vh; */
  padding: 2rem 5rem;
  @media (max-width: 1200px) {
    padding: 2rem 2rem;
  }

  h2 {
    padding-bottom: 2rem;
    color: white;
    font-weight: bold;
  }
`;

export default AboutSection;
