import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

//Project Images
import datawarehouse from "../assets/images/data_warehouse.png";
import ecommerce from "../assets/images/e-commerce.png";
import gifos from "../assets/images/gifos.png";
import landingpage from "../assets/images/landing-page.png";
import newyearcountdown from "../assets/images/newyear-countdown.png";
import reactmusicplayer from "../assets/images/react-music-player.png";
import todoslist from "../assets/images/todos-list.png";

//Icons
import { ArrowForward } from "@material-ui/icons";
//Hooks
import { useScroll } from "../hooks/useScroll";

//Framer Motion
import { motion } from "framer-motion";

//Animations
import {
  pageAnimation,
  fade,
  profilePicAnimation,
  lineAnimation,
  frameAnimation,
  preFrame,
  frameContainer,
} from "../animations/animation";

//Styled components premade
import { Hide } from "../components/styles";

const Projects = () => {
  const [element2, control2] = useScroll();
  const [element3, control3] = useScroll();
  const [element4, control4] = useScroll();
  const [element5, control5] = useScroll();
  const [element6, control6] = useScroll();
  const [element7, control7] = useScroll();

  return (
    <ProjectsContainer
      variants={pageAnimation}
      initial="hidden"
      animate="show"
      exit="exit"
    >
      <motion.div variants={frameContainer}>
        <PreFrame variants={preFrame}></PreFrame>
        <Frame1 variants={frameAnimation}></Frame1>
        <Frame2 variants={frameAnimation}></Frame2>
        <Frame3 variants={frameAnimation}></Frame3>
        <Frame4 variants={frameAnimation}></Frame4>
      </motion.div>

      <Project>
        <motion.div className="headline-container" variants={fade}>
          <motion.h2>Music player</motion.h2>
          <Link to="/projects/music-player">
            <ArrowForward style={{ fontSize: "4rem", color: "black" }} />
          </Link>
        </motion.div>
        <motion.div variants={lineAnimation} className="line"></motion.div>
        <Link to="/projects/music-player">
          <Hide>
            <motion.img
              variants={profilePicAnimation}
              src={reactmusicplayer}
              alt="web page screenshot"
            />
          </Hide>
        </Link>
      </Project>

      <Project ref={element2} animate={control2} variants={fade}>
        <div className="headline-container">
          <h2>E-commerce</h2>
          <Link to="/projects/e-commerce">
            <ArrowForward style={{ fontSize: "4rem", color: "black" }} />
          </Link>
        </div>
        <motion.div variants={lineAnimation} className="line"></motion.div>
        <Link to="/projects/e-commerce">
          <Hide>
            <img src={ecommerce} alt="web page screenshot" />
          </Hide>
        </Link>
      </Project>

      <Project ref={element3} animate={control3} variants={fade}>
        <div className="headline-container">
          <h2>New Year Countdown</h2>
          <Link to="/projects/new-year-countdown">
            <ArrowForward style={{ fontSize: "4rem", color: "black" }} />
          </Link>
        </div>
        <motion.div variants={lineAnimation} className="line"></motion.div>
        <Link to="/projects/new-year-countdown">
          <Hide>
            <img src={newyearcountdown} alt="web page screenshot" />
          </Hide>
        </Link>
      </Project>

      <Project ref={element4} animate={control4} variants={fade}>
        <div className="headline-container">
          <h2>Data warehouse</h2>
          <Link to="/projects/data-warehouse">
            <ArrowForward style={{ fontSize: "4rem", color: "black" }} />
          </Link>
        </div>
        <motion.div variants={lineAnimation} className="line"></motion.div>
        <Link to="/projects/data-warehouse">
          <Hide>
            <img src={datawarehouse} alt="web page screenshot" />
          </Hide>
        </Link>
      </Project>

      <Project ref={element5} animate={control5} variants={fade}>
        <div className="headline-container">
          <h2>Gifos</h2>
          <Link to="/projects/gifos">
            <ArrowForward style={{ fontSize: "4rem", color: "black" }} />
          </Link>
        </div>
        <motion.div variants={lineAnimation} className="line"></motion.div>
        <Link to="/projects/gifos">
          <Hide>
            <img src={gifos} alt="web page screenshot" />
          </Hide>
        </Link>
      </Project>

      <Project ref={element6} animate={control6} variants={fade}>
        <div className="headline-container">
          <h2>Todo list</h2>
          <Link to="/projects/todos">
            <ArrowForward style={{ fontSize: "4rem", color: "black" }} />
          </Link>
        </div>
        <motion.div variants={lineAnimation} className="line"></motion.div>
        <Link to="/projects/todos">
          <Hide>
            <img src={todoslist} alt="web page screenshot" />
          </Hide>
        </Link>
      </Project>

      <Project ref={element7} animate={control7} variants={fade}>
        <div className="headline-container">
          <h2>Podcast landingpage</h2>
          <Link to="/projects/podcast-landing-page">
            <ArrowForward style={{ fontSize: "4rem", color: "black" }} />
          </Link>
        </div>
        <motion.div variants={lineAnimation} className="line"></motion.div>
        <Link to="/projects/podcast-landing-page">
          <Hide>
            <img src={landingpage} alt="web page screenshot" />
          </Hide>
        </Link>
      </Project>
    </ProjectsContainer>
  );
};
//This is the way to apply motion to an styled component
const ProjectsContainer = styled(motion.div)`
  overflow: hidden;
  padding: 0rem 10rem;
  background: white;

  @media (max-width: 1200px) {
    padding: 0rem 2rem;
  }
`;
//Simple styled component
const Project = styled(motion.div)`
  padding: 4rem 0rem;
  max-height: 90vh;
  min-height: 50vh;

  .headline-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 2rem;

    h2 {
      line-height: 6rem;
    }
  }
  .line {
    height: 5px;
    background: #23d997;
    margin-bottom: 3rem;
  }
  img {
    width: 100%;
    height: 60vh;
    object-fit: contain;
  }

  @media (max-width: 1200px) {
    img {
      width: auto;
      max-width: 100%;
      max-height: 40vh;
      height: auto;
      object-fit: contain;
    }
  }
`;

//For Frame Animation
const PreFrame = styled(motion.div)`
  position: fixed;
  left: 0;
  top: 10%;
  width: 100%;
  height: 100vh;
  background-color: #1b1b1b;
  z-index: 2;
`;
const Frame1 = styled(motion.div)`
  position: fixed;
  left: 0;
  top: 10%;
  width: 100%;
  height: 100vh;
  background-color: #2e2e2e;
  z-index: 2;
`;
const Frame2 = styled(Frame1)`
  background-color: #444444;
`;
const Frame3 = styled(Frame1)`
  background-color: #6b6b6b;
`;
const Frame4 = styled(Frame1)`
  background-color: #23d997;
`;

export default Projects;
