import React from "react";

import { withStyles } from "@material-ui/core/styles";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

const AccordionStyled = withStyles({
  root: {
    backgroundColor: "transparent",
    margin: "3rem 0rem",
    width: "100%",
    borderRadius: ".2rem",
    borderBottom: "solid .3rem white",
    padding: ".5rem",
  },
})(Accordion);
const AccordionDetailsStyled = withStyles({
  root: {
    display: "flex",
    flexDirection: "column",
  },
})(AccordionDetails);

const MoreAbout = ({ title, description }) => {
  return (
    <AccordionStyled>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon fontSize="large" />}
        aria-controls="panel1a-content"
        id="panel1a-header"
      >
        <h3 style={{ fontSize: "1.5rem" }}>{title}</h3>
      </AccordionSummary>
      <AccordionDetailsStyled>
        {description.map(({ desc, small, index }) => (
          <div key={desc} style={{ marginBottom: "1.5rem" }}>
            <p style={{ marginBottom: ".5rem" }}>-{desc}</p>
            <span>{small}</span>
          </div>
        ))}
      </AccordionDetailsStyled>
    </AccordionStyled>
  );
};

export default MoreAbout;
