//Styled components
import styled from "styled-components";

export const About = styled.div`
  min-height: 90vh;
  /* max-height: 90vh; */
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  /* Quitar en mobile */
  padding: 5rem 10rem;

  @media (max-width: 1200px) {
    display: block;
    padding: 5rem 0rem;
    text-align: center;
    /* min-height: auto; */
  }
`;
export const Description = styled.div`
  flex: 1;
  /* Quitar en mobile */
  padding-right: 5rem;

  & div:last-child {
    margin-bottom: 2rem;
  }
  @media (max-width: 1200px) {
    padding-right: 0;
    p {
      padding: 0 2rem;
    }
  }
  h2 {
    font-weight: lighter;
  }

  p {
    margin-bottom: 2rem;
    a {
      text-decoration: none;
      color: #23d997;
      font-size: 1.4rem;
      font-weight: bold;
      line-height: 150%;
      transition: 0.5s all;

      &:hover {
        font-size: 1.5rem;
      }
    }
  }
`;
export const Image = styled.div`
  flex: 1;
  overflow: hidden;

  img {
    width: 100%;
    height: 70vh;
    max-height: 70vh;
    object-fit: scale-down;
    filter: grayscale(1);

    @media (max-width: 1200px) {
      object-fit: contain;
      width: 100%;
      height: 40vh;
      max-height: 40vh;
      background-color: transparent;
    }
  }
`;
export const Hide = styled.div`
  overflow: hidden;
`;
