import React from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";
import { motion } from "framer-motion";
import { useLocation } from "react-router-dom";

const Nav = () => {
  const { pathname } = useLocation();

  return (
    <StyledNav>
      <ul>
        <li>
          <NavLink to="/" exact>
            <h4>About me</h4>
          </NavLink>
          <Line
            transition={{ duration: 0.45 }}
            initial={{ width: "0%" }}
            animate={{ width: pathname === "/" ? "100%" : "0%" }}
          />
        </li>
        <li>
          <NavLink to="/projects">
            <h4>Projects</h4>
          </NavLink>
          <Line
            transition={{ duration: 0.45 }}
            initial={{ width: "0%" }}
            animate={{ width: pathname === "/projects" ? "100%" : "0%" }}
          />
        </li>
        <li>
          <NavLink to="/skills">
            <h4>Skills</h4>
          </NavLink>
          <Line
            transition={{ duration: 0.45 }}
            initial={{ width: "0%" }}
            animate={{ width: pathname === "/skills" ? "100%" : "0%" }}
          />
        </li>
      </ul>
    </StyledNav>
  );
};

const StyledNav = styled.nav`
  min-height: 10vh;
  max-height: 10vh;
  display: flex;
  align-items: center;
  padding: 1rem;
  background-color: #282828;
  position: sticky;
  top: 0;
  z-index: 10;

  a {
    color: white;
    text-decoration: none;
  }
  ul {
    display: flex;
    justify-content: space-around;
    list-style: none;
    flex: 1;
    li {
      position: relative;
      h4 {
        letter-spacing: 0.1rem;
        text-align: start;
        padding: 0 2rem 0 0.3rem;
      }
    }
  }
`;
const Line = styled(motion.div)`
  height: 0.3rem;
  background-color: #23d997;
  position: absolute;
  bottom: -60%;
  left: 0;
`;

export default Nav;
