import React, { useState, useEffect, useRef } from "react";
//History
import { useHistory } from "react-router-dom";
//JSON data
import ProjectsInfo from "../assets/JSON/ProjectsInfo";
//Styled Comps
import styled from "styled-components";
//Icons Material UI
import { GitHub, Language } from "@material-ui/icons";
//Framer Motion
import { motion } from "framer-motion";
//Animations
import { pageAnimation } from "../animations/animation";
import Tooltip from "@material-ui/core/Tooltip";

const ProjectDetails = () => {
  // useHistory
  const history = useHistory();
  const url = history.location.pathname;
  //Loading personal projects data
  const [project, setProject] = useState([]);
  const [loadedProject, setLoadedProject] = useState(false);

  useEffect(() => {
    const projectsInfo = ProjectsInfo();
    const currentProject = projectsInfo.filter((proj) => proj.url === url);
    setProject(currentProject[0]);
    setLoadedProject(true);
  }, [url]);

  //Ref
  const videoRef = useRef();

  const autoPlayHandler = () => {
    if (loadedProject) {
      videoRef.current.play();
      videoRef.current.muted = false;
    }
  };

  return (
    <DetailsContainer
      variants={pageAnimation}
      initial="hidden"
      animate="show"
      exit="exit"
    >
      <Headline>
        {/* Once the project is fully loaded I call the video tag */}
        {loadedProject && (
          <video
            controls
            ref={videoRef}
            onLoadedMetadata={autoPlayHandler}
            muted
          >
            <source src={project?.mainVideo} type="video/mp4" />
          </video>
        )}
        <div className="title-icons-wrapper">
          <h2>{project?.title}</h2>
          <div className="icon-container">
            <Tooltip title="Go github">
              <a
                href={project?.githubUrl}
                target="_blank"
                rel="noopener noreferrer"
              >
                <GitHub style={{ fontSize: "3rem" }} />
              </a>
            </Tooltip>
            <Tooltip title="Go website">
              <a
                href={project?.websiteUrl}
                target="_blank"
                rel="noopener noreferrer"
              >
                <Language style={{ fontSize: "3.6rem" }} />
              </a>
            </Tooltip>
          </div>
        </div>
        <div className="line"></div>
      </Headline>
      <div className="description">
        <h4>{project?.description}</h4>
      </div>
    </DetailsContainer>
  );
};

const DetailsContainer = styled(motion.div)`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  color: white;
  padding: 2rem 0;

  .description {
    width: 60vw;
    margin-top: 2rem;
  }
  @media (max-width: 1200px) {
    padding: 7rem 0;
    .description {
      width: 90%;
      margin-top: 4rem;
    }
  }
`;
const Headline = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: auto;

  video {
    width: 60vw;
    object-fit: contain;
    outline: none;
    object-fit: fill;
    box-shadow: 0 3px 10px 1px black;
    @media (max-width: 1200px) {
      width: 100%;
      padding: 0 1rem;
    }
  }

  .title-icons-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
    width: 100%;
    h2 {
      flex: 1;
    }
    @media (max-width: 1200px) {
      width: 90%;
      h2 {
        margin-top: 2rem;
      }
    }
    .icon-container {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      a {
        color: inherit;
        transition: 0.3s ease-in-out;
        margin-left: 2rem;
        margin-right: 1rem;

        &:hover {
          transform: scale(1.2);
          color: #23d997;
        }
      }
    }
  }
  .line {
    margin-top: 0.3rem;
    background-color: #23d997;
    height: 0.3rem;
    width: 100%;
    align-self: flex-start;
    @media (max-width: 1200px) {
      width: 90%;
      align-self: center;
      margin-top: 2rem;
    }
  }
`;

export default ProjectDetails;
