import { createGlobalStyle } from "styled-components";

const GlobalStyle = createGlobalStyle`

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background: #1b1b1b;
    font-family: 'Inter', sans-serif;
    overflow-x: hidden;
    
}
html {
    @media (max-width: 1400px ){
        font-size: 80%;
    }
    @media (max-width: 1200px ){
        font-size: 55%;
    }
    /* @media (max-width: 1000px ){
        font-size: 70%;
    }
    @media (max-width: 768x ){
        font-size: 60%;
    } */

}
h2{
    font-weight: lighter;
    font-size: 4rem;
}
h3{
    color: white;
}


h4 {
    font-weight: bold;
    font-size: 1.7rem;
}
a{
    font-size: 1.1rem;
}
span {
    font-weight: bold;
    color: #23d997;
}
p{
    color: #ccc;
    font-size: 1.4rem;
    line-height: 150%;
} 

`;

export default GlobalStyle;
