import React from "react";

//Images
import htmlPic from "../assets/images/html.png";
import cssPic from "../assets/images/css.png";
import jsPic from "../assets/images/js.png";
import reactPic from "../assets/images/react.png";
import mongoPic from "../assets/images/mongo.png";
import nodePic from "../assets/images/node.png";
import postmanPic from "../assets/images/postman.png";
import corelPic from "../assets/images/coreldraw.png";
import sassPic from "../assets/images/sass.png";
import mariaDbPic from "../assets/images/mariadb.png";

//Framer Motion
import { motion } from "framer-motion";
//Animations
import { pageAnimation } from "../animations/animation";
//Styled Components
import styled from "styled-components";

//Components
import FlipingCard from "../components/FlipingCard";

const Skills = () => {
  return (
    <motion.div
      variants={pageAnimation}
      initial="hidden"
      animate="show"
      exit="exit"
    >
      <CardsContainer>
        <FlipingCard
          picture={htmlPic}
          description="I like developing a clean and well indented structure in order to facilitate
later work."
        />
        <FlipingCard
          picture={cssPic}
          description="An aspect in which I find myself coding really fast. Designing experience
helps me now turn a design concept into an actual code, rapidly."
        />
        <FlipingCard
          picture={jsPic}
          description="Thanks for existing Javascript! Using it for both front and backend, constantly learning about it."
        />
        <FlipingCard
          picture={reactPic}
          description="I start learning React in November 2020, feeling in love with it. Less code, more functionality!"
        />
        <FlipingCard
          picture={mongoPic}
          description="Models, refs, simple populations, all crud methods. Mongoose as ODM. Still a lot to learn about this giant."
        />
        <FlipingCard
          picture={nodePic}
          description="Using it for React, using it for doing API´s, amazing tool to learn a lot more about it too."
        />
        <FlipingCard
          picture={postmanPic}
          description="Use of this tool to test APIs and do documentation."
        />
        <FlipingCard
          picture={corelPic}
          description="Advanced level. As it is a tool I have been using for 6 years, I can rapidly
create a draft from an idea that was provided to me. Basic Photoshop and Illustrator level."
        />
        <FlipingCard
          picture={sassPic}
          description="Variables, mixins, nestings and selector inheritance is why I use Sass almost al the time."
        />
        <FlipingCard
          picture={mariaDbPic}
          description="Knowledge of queries developing for all CRUD methods.
Able to create simple databases."
        />
      </CardsContainer>
    </motion.div>
  );
};
const CardsContainer = styled.div`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3rem;
  padding: 3rem 0;
`;

export default Skills;
