import React, { useEffect } from "react";

//Global Style
import GlobalStyle from "./components/GlobalStyle";
//Components
import Nav from "./components/Nav";
import About from "./pages/About";
import Projects from "./pages/Projects";
import ProjectDetails from "./pages/ProjectDetails";
import Skills from "./pages/Skills";

//Router
import { Switch, Route, useLocation } from "react-router-dom";

//Animation
// AnimatePresence allows components to animate out when they are removed from the React tree
import { AnimatePresence } from "framer-motion";

function App() {
  const location = useLocation();

  //To scroll to top on any location change, to alway see the top of the page first
  useEffect(() => {
    setTimeout(() => {
      window.scroll({
        top: 0,
        left: 0,
      });
    }, 500);
  }, [location]);

  return (
    <div className="App">
      <GlobalStyle />
      <Nav />
      <AnimatePresence exitBeforeEnter>
        <Switch location={location} key={location.pathname}>
          <Route path="/" exact component={About} />
          <Route path="/projects" exact component={Projects} />
          <Route path="/projects/:id" exact component={ProjectDetails} />
          <Route path="/skills" component={Skills} />
        </Switch>
      </AnimatePresence>
    </div>
  );
}

export default App;
