import React from "react";
//Components
import AboutSection from "../components/AboutSection";

//Framer Motion
import { motion } from "framer-motion";

//Animations
import { pageAnimation } from "../animations/animation";

const About = () => {
  return (
    <motion.div
      variants={pageAnimation}
      initial="hidden"
      animate="show"
      exit="exit"
    >
      <AboutSection />
    </motion.div>
  );
};

export default About;
